body,
p,
h1,
h2,
h3 {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Proxima Nova", Georgia, sans-serif;
  background: rgb(24, 24, 24);
  color: #fff;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0;
}

.app-container {
  display: flex;
  position: relative;
}

.left-side-section {
  position: fixed;
  top: 10px;
  left: 10px;
  bottom: 10px;
  width: 200px;
  overflow-y: auto;
}

.main-section {
  width: 900px;
  position: relative;
  left: 200px;
  padding: 0 10px;
}

.main-section-container {
  position: relative;
  top: 50px;
  padding-bottom: 100px;
}
